:root {
  /* PALETA DE COLORES */
  --principal: hsla(0, 0%, 90%, 0.671);
  --secundario: #497ea3;
  --terciario: hsl(0, 0%, 85%);
  --cuaterciario: hsl(0, 0%, 15%);
  --quinto: #3b4ba5;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body, html {
  background-color: var(--principal);
  color: var(--cuaterciario);
  text-align: center;
}

.App {
  margin: auto;
  width: 500px;
  display: flex;
}

.calculator {
  margin-top: 10px;
  background-color: var(--secundario);
  border: 1px solid var(--cuaterciario);
  flex: 1 1;
  border-radius: 10px;
}

.calculator_row {
  display: flex;
  align-items: center;
  justify-content: center;
}

.screen {
  flex: 1 1; 
}

footer {
  text-align: center;
  position: fixed;
  bottom: 2%;
}

.link {
  color: var(--cuaterciario);
}

@media screen and (max-width: 525px) {

  .App {
    width: 95%;
  }
}

@media screen and (max-width: 330px) {
  .App {
    width: 100%;
  }
}